import React, { FC, CSSProperties, useState } from 'react';
import { Copy } from 'react-feather';

import styles from './CopyIcon.module.scss';

const ICON_SIZE_MAP = {
  xs: 8,
  sm: 12,
  md: 16,
  lg: 20,
};

interface ICopyIcon {
  value: string;
  style?: CSSProperties;
  size?: 'xs' | 'sm' | 'md' | 'lg';
  onCopy: (value: string) => void;
}

const CopyIcon: FC<ICopyIcon> = ({ value, style, onCopy, size = 'sm' }) => {
  const [copied, setCopied] = useState(false);

  return (
    <span data-testid="copy-icon" className={styles['copy-icon']}>
      <Copy size={ICON_SIZE_MAP[size]} style={style} />
    </span>
  );
};

export type { ICopyIcon };
export { CopyIcon };
